.DashBoard{
    margin-right: -3px;
    margin-top: 7px;
    border:3px solid whitesmoke;
   
    border-radius: 5px;
    position: absolute;
    top: 0;
    right: 0;
    width: 965px;
    height: 567px;
    padding-left:20px ; 
    font-style:italic;
    color: white;
    text-shadow: 3px;
    font-weight: bolder;
}

.Chart{
    height: 250px;
  width: 531px;
  margin-left: 200px;
 border:3px solid whitesmoke;
  border-radius: 5px;
  padding: 31px;
  display: block;
}
.Total{
    display:flex ;
    margin-left:167px ;

}
 .cont{
    width:200px ; 
    height:70px ; 
    margin-top: 30px;
    margin-left: 65px;
    border-radius:5px ; 
    padding-left:7px ; 

 }

 .cont p {
    margin-top: 13px;
    font-size: 21px;
    font-weight: bolder;
    font-style: italic;
    line-height: 8px;
 }
 .TotalIncome{
  border:3px solid rgb(90, 187, 232);
  
 }
 .TotalExpense{
  border:3px solid rgb(225, 234, 47);
 }
 @media screen and (max-width:768px) {
   .DashBoard{
      text-align: center;
      width: 465px;
     
   }
   .Chart{
      width: 407px;
    height: 197px;
   }
   .Dash{
      margin-left:-376px ;
      padding-left: 159px;
   }
   .Total{
      margin-left: 162px;
    padding-right: 36px;
   }
 }
 @media screen and (max-width:480px) {
   .DashBoard{
      margin-top: 587px; 
      width: 455px;
   }
 }
 